.nav {
    position: fixed;
    top: 0;
    background : linear-gradient(#111, transparent);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 30px;
    padding: 20px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__black {
    background-color: #111;
}
.nav__logo {
    position: fixed;
    left: 40px;
    width: 80px;
    object-fit: contain;
    cursor:pointer;
}

.nav__avatar {
    position: fixed;
    right: 40px;
    width: 30px;
    object-fit: contain;
}

.nav__input {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: rgba(16, 1, 1, 0.562);
    color: white;
    padding : 5px;
    border: none;
}

